.gallery {
    border: 1px solid #000000;
    display: block;
    height: 100vh;
    overflow-y: auto;
    background-color: rgb(206, 230, 252);
    text-align: center;
  }

.image {
    border: 5px solid #969696;
    width: 200px;
    height: 200px;
    margin: 5px;
  }

.selected-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 300px;
  height: 200px;
}

.modalBackdrop {
  background-color: rgba(0, 0, 0, 0.6); /* semi-transparent black overlay */
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.modal {
  background-color: white;
  width: 70%;
  height: 70%;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.modalContent p:first-of-type {
  font-size: 2.5rem;
  font-weight: bold;
  text-align: center;
}

.modalContent p {
  font-size: 1.5rem;
}

.modalHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.closeButton {
  border: none;
  background-color: black;
  font-size: 1.5rem;
  cursor: pointer;
}

.modalContent {
  margin-top: 20px;
}